*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  display: block;
}

.title {
  color: #212121;
  margin-top: 0;
  margin-bottom: 0;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  cursor: pointer;
  margin: 0;
  text-decoration: none;
  display: inline-flex;
}

.button {
  cursor: pointer;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
}

.page {
  color: #757575;
  background-color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.address {
  font-style: normal;
}

.address__post-address {
  letter-spacing: .03em;
  color: #fff;
  margin-bottom: 12px;
  line-height: 1.14;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.address__post-address:hover, .address__post-address:focus {
  color: #2196f3;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition-property: visibility, opacity;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop__modal {
  opacity: 1;
  transition-property: transform, opacity;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.backdrop--is-hidden .backdrop__modal {
  opacity: 0;
  transform: translate(-50%);
}

.button-icon {
  justify-content: center;
  align-items: center;
  display: flex;
}

.callback__label {
  cursor: pointer;
  flex-direction: column;
  margin-bottom: 10px;
  display: flex;
}

.callback__label--lower {
  margin-bottom: 20px;
}

.callback__checkbox {
  margin-bottom: 31px;
}

@media screen and (min-width: 1200px) {
  .callback__checkbox {
    margin-bottom: 30px;
  }
}

.callback__button {
  width: 200px;
  height: 50px;
  letter-spacing: .06em;
  color: #fff;
  background-color: #2196f3;
  border: none;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.callback__button:hover, .callback__button:focus {
  background-color: #188ce8;
}

.checkbox {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
}

.checkbox__custom-icon {
  width: 16px;
  height: 15px;
  margin-right: 8px;
  position: relative;
}

.checkbox__check-frame {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition-property: opacity;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.checkbox__input:checked ~ .checkbox__check-frame {
  opacity: 0;
}

.checkbox__check-icon {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition-property: opacity;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.checkbox__input:checked ~ .checkbox__check-icon {
  opacity: 1;
}

.checkbox__article {
  letter-spacing: .03em;
  font-size: 12px;
  line-height: 1.17;
}

@media screen and (min-width: 1200px) {
  .checkbox__article {
    font-size: 14px;
    line-height: 1.71;
  }
}

.checkbox__link {
  color: #2196f3;
  text-decoration: underline;
}

.choise-buttons {
